import React,{useContext,useState,useEffect,Suspense, lazy} from 'react';
import { MyContext } from '../../contexts/MyContext';
import { FiLogIn} from "react-icons/fi";
import ReCAPTCHA from "react-google-recaptcha";

import { CAPTCHA_SITE_KEY} from '../../contexts/Apilinks';
import {Link} from 'react-router-dom';
import Loader from 'react-loader-spinner';
import { useHistory } from "react-router-dom";
import {toast} from "react-toastify";
import '../../styles/_login.scss';
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import PhoneInput from 'react-phone-input-2'
import 'react-phone-input-2/lib/style.css'


const TEST_SITE_KEY = CAPTCHA_SITE_KEY;
const DELAY = 1500;

const Register = (props) => {
       let history = useHistory();

       const {RegisterUser} = useContext(MyContext);
    
  const [value, setvalue] = useState("[empty]");
  const [load, setload] = useState(false);
  const [recaptchaLoaded, setrecaptchaLoaded] = useState(false);


  const [loadinglogin, setloadinglogin] = useState(false);
  const _reCaptchaRef = React.createRef()
  const [userInfo, setuserInfo] = useState({
    name: '',
    email: '',
    password: '',
    type: '',
    conformpassword: '',
    subscribe: '',
});
const onChangeValue = (e) => {
    setuserInfo({
        ...userInfo,
            [e.target.name]:e.target.value
    });
} 


  useEffect( () => {
    window.scrollTo(0, 0);
    setTimeout(() => {
      setload(true );
    }, DELAY);
    console.log("didMount - reCaptcha Ref-", _reCaptchaRef);
   }, []); 

 
  const handleChange = (value) => {
    setvalue(value);
   
  };
  const [phone, setphone] = useState('');
  const onChangeValuephone = (value) => {
    setphone(value);
   
  };
  const asyncScriptOnLoad = () => {
    setrecaptchaLoaded(true);
    _reCaptchaRef.current.execute();
  };
  


  const Register = async (event) => {
    
       setloadinglogin(true);
       event.preventDefault();
       event.persist();
       if(userInfo.password !== userInfo.conformpassword){
         toast.error('password and confirm password does not match.',{
           position: "top-center",autoClose: 7000,hideProgressBar: false, closeOnClick: true,pauseOnHover: true,draggable: true,
         });  
         setloadinglogin(false);
         return;
       }
      
        if (phone.length < 9) {
          toast.error('Please enter only number.',{
            position: "top-center",autoClose: 7000,hideProgressBar: false, closeOnClick: true,pauseOnHover: true,draggable: true,
          });  
          setloadinglogin(false);
          return;
        }
       
      
     
       //const data = await RegisterUser(userInfo,userInfo.recaptcha_response.value,ispennameActive);
       const data = await RegisterUser(userInfo,phone);
       if(data.success === 1){
         setloadinglogin(false);
          toast('Account successfully created. Thank you for your registration! Your account is now ready to use.',{position: "top-center",autoClose: 10000,});
         event.target.reset(); 
         history.push('/Login');
        
       }
       else{
         setloadinglogin(false);
         toast.error( data.msg, {
             position: "top-center",autoClose: 5000,hideProgressBar: false, closeOnClick: true,pauseOnHover: true,draggable: true,
           });  
     }
     
     }
    
       return (
              <>
          <Navbarmenu />
     
   <div className="Loginone mins">
      <div className="container">
         <div className="row">
       
         <div className="col-md-8">
         <div className="login-forms">  
         
         <h1> Sign Up  </h1>
        
         <form onSubmit={Register} >
        
                <div className="clearfix"></div>
            <div className="form-row">
           
                  <div className="clearfix"></div>
                <div className="form-group col-md-6">
                    <label className="font-weight-bold">Name <span className="required">*</span></label>
                    <input type="text" name="name" value={userInfo.name} onChange={onChangeValue}className="form-control" placeholder="Enter a profile name." required   />
                   
                </div> 
               
                <div className="form-group col-md-6">
                    <label className="font-weight-bold">What's your email? <span className="required">*</span></label>
                    <input type="email" name="email" value={userInfo.email} onChange={onChangeValue}className="form-control" placeholder="Enter your email." required />
                </div>
                <div className="clearfix"></div>
                <div className="form-group col-md-6">
                    <label className="font-weight-bold"> Create a password <span className="required">*</span> </label>
                    <input type="password" name="password" value={userInfo.password} onChange={onChangeValue}className="form-control" placeholder="Type to create a password"  required />
                    
                </div>
                <div className="form-group col-md-6">
                    <label className="font-weight-bold"> Password repeat <span className="required">*</span></label>
                    <input type="password" name="conformpassword" value={userInfo.conformpassword} onChange={onChangeValue} className="form-control" placeholder="Type to create a password" required />
                </div>
                <div className="clearfix"></div>
                <div className="form-group col-md-6">
                    <label className="font-weight-bold">Role <span className="required">*</span></label>
                      <select  name="type" value={userInfo.type} onChange={onChangeValue} className="select__single form-control" required >
                              <option value=""> Select </option>
                              <option value="Buyer/Owner">Buyer/Owner </option>
                              <option value="Author">Agent</option>
                              <option value="other">Builder</option>
                            </select>
                            
                  </div>
                  <div className="form-group col-md-6">
                    <label className="font-weight-bold"> Mobile number <span className="required">*</span></label>
                    <PhoneInput
                    inputProps={{
                      name: 'phone',
                      required: true,
                      autoFocus: true
                    }}
                        country={'in'}
                        value={phone}
                        onChange={onChangeValuephone}
                    />
                    
                            
                  </div>

                <input type="hidden" name="recaptcha_response" ref={(val) => userInfo.recaptcha_response = val} className="form-control" placeholder="Your password here..." required  defaultValue={value} /> 

                {load && (
          <ReCAPTCHA
            style={{ display: "inline-block" }}
            theme="dark"
            size="invisible"
            ref={_reCaptchaRef}
            sitekey={TEST_SITE_KEY}
            onChange={handleChange}
            asyncScriptOnLoad={asyncScriptOnLoad}
          />
        )} 

              <div className="form-group col-md-12 checksss2">
			<label className="" >
			  <input type="checkbox" className="checksss" name="subscribe" value="yes" onChange={onChangeValue}/>
                      <span style={{verticalAlign: "sub"}}>Share my registration data with bookois content providers for marketing purposes. </span>
			  <div className="check"></div>
        
			</label>
		</div>
                    <div className="form-group col-md-12 ">
                    {loadinglogin ?
                            <>
                                  <Loader type="Oval" color="#00bf6f" height={47} width={47} style={{float: "left"}}/>
                            </>
                         : 
                        <button type="submit" className="log__btn" disabled={!recaptchaLoaded}> Submit <span className="log__icon"> <FiLogIn /> </span> </button>
                    } 
                    </div>
                </div>
               
                
            </form>  
            <div className="clearfix"></div>
            <p className="forgggsss2"> <Link to="/Login" >  Already have an Account ? Login here  </Link> </p>
            
            </div>
            </div>
            <div className="col-md-6">
         
            </div>
        
       


</div>



      </div>
   </div>
          <Footer />
                     
              </>
       )
}

export default Register
